.theimg{
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    font-size: 0;
    overflow: hidden;
}
.theimg img {
    width: 100%;
    height: auto;
}
.bottom-bar{
    position: fixed;
    bottom: 0;
    width: 100%;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    z-index: 110;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
    flex-direction: row;
    -webkit-box-align: center;
    -webkit-align-items: center;
    align-items: center;
    background-color: #fff;
    /* box-shadow: rgba(0, 0, 0, 0.15) 0 -0.01rem 0.06rem 0; */
    padding-left: 5px;
    padding-right: 5px;
    /* padding-bottom: env(safe-area-inset-bottom);
    padding-bottom: constant(safe-area-inset-bottom); */
}
.bottom-bar > div:first-child {
    padding-left: 0;
    margin-left: 0;
}
.bottom-bar > div {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    width: 70px;
    height: 50px;
    white-space: nowrap;
    -webkit-box-align: center;
    -webkit-align-items: center;
    align-items: center;
    color: #999;
    padding: 0 0.1rem;
}
.text{
    font-size: 10px;
    line-height: 20px;
    /* text-align: center; */
}
.cart {
    border-radius: 20px 0 0 20px;
    background-image: -webkit-linear-gradient(left, #FFC500, #FF9402);
    background-image: linear-gradient(to right, #FFC500, #FF9402);
}
.iconfont{
    width: 20px;
    height: 20px;
}
.gtitle {
    font-size: 14px;
    line-height: 20px;
    font-weight: bold;
    color: white;
}
.buy{
    border-radius: 0 20px 20px 0;
    background-image: -webkit-linear-gradient(left, #FF7A00, #FE560A);
    background-image: linear-gradient(to right, #FF7A00, #FE560A);
}
.price{
    font-size: 25px;
    color: red;
}
.dirtxt{
    font-size: 20px;
}