* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: 0;
    -webkit-tap-highlight-color: transparent;
}
.home{
    position: relative;
    margin: 0 auto;
    /* height: 100%; */
}
.theTop{
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 1000;
    width: 100%;
    background-color: rgb(255, 133, 42);
}
.searchbar{
    width: 90%;
    padding-right: 9px;
    padding-left: 42px;
    padding-top: 6px;
    padding-bottom: 6px;
    background-image: url(./image/tao.png);
    background-size: 30px 30px;
    background-repeat: no-repeat;
    background-position: 9px center;
}

.search {
    display: block;
    font-size: 0px;
    background-color: rgb(255, 78, 34);
    text-align: center;
    border-radius: 4px;
}
.search span{
    height: 25px;
    line-height: 25px;
    font-size: 13px;
    display: inline-block;
    color: rgb(240, 224, 220);
}
.Carousel{
    margin-top: 40px;
    height: 120px;
}
.goods{
     display: -webkit-flex;
    display: flex; 
    position: relative;
    overflow: hidden;
    flex-wrap: wrap;
    padding: 0 7.5px;
    background: #f2f2f2;
    padding-bottom: 12px;
}
