.image{
    -webkit-box-align: center;
    width: 170px;
    height: 170px;
    align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
}
.pic{
    margin: 0;
    width: 170px;
    height: 170px;
}
.title{
    padding-left: 10px;
    padding-right: 9px;
    height: 94px;
    text-decoration: none;
}
.txt{
    margin-top: 11.5px;
    height: 37px;
    position: relative;
    overflow: hidden;
    line-height: 18.5px;
    text-overflow: ellipsis;
    min-height: 0;
}
.txt span{
    font-size: 13px;
    color: #333333;
}
.money{
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
    flex-direction: row;
    margin-top: 11px;
    height: 20px;
    line-height: 20px;
}
.money1{
    font-size: 11px;
    color: #ff5500;
}
.money2{
    font-size: 14px;
    line-height: 16px;
    color: #ff5500;
}
.money3{
    font-size: 11px;
    color: #999999;
    margin-top: 2px;
}
.footer{
    margin-top: 20px;
    padding: 20px 9px 56px;
    background-color: rgb(78, 82, 94);

}
.bottom{
    width: 100%;
    display: -webkit-flex;
    -webkit-box-pack: justify;
    justify-content: space-between;      
}
.bottom a {
    display: inline-block;
    height: 35px;
    line-height: 35px;
    color: rgb(195, 198, 202);
    border: 1px solid rgb(66, 69, 77);
    background-color: rgb(85, 89, 101);
    padding: 0px 17px;
    border-radius: 6px;
    font-size: 14px;
}